import React from 'react';
import {Button, Intent} from '@blueprintjs/core';
import {withRouter} from 'react-router-dom';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {addCenter, loadCenters} from '../../../actions/CentersActions';
import {isFormValid} from "../../../helpers";
import Toaster from "../../Common/Toaster";
import {ADD_CENTER} from "../../../helpers/constants";
import _isEqual from "lodash/isEqual";

class NewCenter extends React.Component{

    state = {
        name: '',
        code: '',
    };

    componentDidUpdate(prevProps){
        const {OPERATION_SUCCESSFUL: OLD_OPERATION_SUCCESSFUL, OPERATION_FAILED: OLD_OPERATION_FAILED} = prevProps;
        const {OPERATION_SUCCESSFUL, OPERATION_FAILED, history, authUser: {token}, loadCenters} = this.props;

        const center = this.state.name.toUpperCase();

        if(!_isEqual(OLD_OPERATION_SUCCESSFUL, OPERATION_SUCCESSFUL)){
            loadCenters(token, {});
            
            if(OPERATION_SUCCESSFUL.action === ADD_CENTER){
                history.goBack();
                Toaster.show({
                    message: `${center} Successfully Added 😃`,
                    intent: Intent.SUCCESS,
                    icon: 'tick' });
            }
        }

        if(!_isEqual(OLD_OPERATION_FAILED, OPERATION_FAILED)){
            if(OPERATION_FAILED.action === ADD_CENTER){
                console.log(OPERATION_FAILED.data);
                Toaster.show({
                    message: `Could Not Add ${center} 😞`,
                    intent: Intent.DANGER,
                    icon: 'error'
                });
            }
        }
    }

    _handleInputChange = (name, value) => {
        this.setState({[name]: value});
    };

    _handleAddCenter = () => {
        const {addCenter, authUser: {token}} = this.props;
        const {name, code} = this.state;

        let data = new FormData();

        data.append('name', name);
        data.append('code', code);

        addCenter(token, data)
    };

    _isFormValid(){
        const {name, code} = this.state;

        let required = {name, code};

        return isFormValid(required);
    }

    render(){
        const {addingCenter} = this.props;
        const {name, code} = this.state;
        const disableSaveButton = !this._isFormValid();

        return (
            <div className="">

                <label className="bp3-label">
                    Name
                    <input
                        value={name}
                        name={'name'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" type="text" dir="auto" />
                </label>
                <label className="bp3-label">
                    Code
                    <input
                        value={code}
                        name={'code'} onChange={({target: {name, value}}) => this._handleInputChange(name, value)}
                        className="bp3-input bp3-fill" dir="auto" />
                </label>

                <Button
                    disabled={disableSaveButton}
                    loading={addingCenter}
                    intent={Intent.PRIMARY}
                    onClick={this._handleAddCenter}
                    icon="tick"
                    text="save"
                />
            </div>
        );
    }

    static propTypes = {
        history: PropTypes.object.isRequired,
        authUser: PropTypes.object.isRequired,
        OPERATION_SUCCESSFUL: PropTypes.object.isRequired,
        OPERATION_FAILED: PropTypes.object.isRequired,
        addCenter: PropTypes.func.isRequired,
        loadCenters: PropTypes.func.isRequired,
        addingCenter: PropTypes.bool.isRequired,
    };
}

const mapStateToProps = (
    {OPERATION_SUCCESSFUL, OPERATION_FAILED, addingCenter, authUser}) => (
        {OPERATION_SUCCESSFUL, OPERATION_FAILED, addingCenter, authUser});

const mapDispatchToProps = dispatch => bindActionCreators({addCenter, loadCenters}, dispatch);

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(NewCenter));


